!(function () {
  let vw = window.innerWidth;

  const adjustVh = () => {
    const vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  };

  window.addEventListener('resize', () => {
    if (vw === window.innerWidth) {
      // 
      return;
    }
    // 
    vw = window.innerWidth;
    adjustVh();
  });

  // 
  adjustVh();
})();
